<template>
    <div class="background">
        <div class="background__blured-image">
            <img :src="getPoster()" alt="" class="img">
        </div>
        <div class="background__immersive-gradient"></div>
    </div>
</template>

<script>
import MovieList from '@/assets/data/movie.json';

export default {
    props: {
        poster: String
    },
    methods: {
        getPoster(){
            const poster = this.poster;
            const image = MovieList.filter(function(item){
                return item.title === poster;
            }).map(function(item){
                return require("@/assets/images/poster/" + item.poster)
            });

            return image;
        }
    },
    mounted() {
    }
}
</script>
<style lang="scss" scoped>
.background {
    z-index: -1;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    
    &__blured-image{
        width: 100%;
        height: 100%;
        filter: blur(40px);
        opacity: 0.6;

        .img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__immersive-gradient{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(to bottom, rgba(62, 67, 80, 0.8) 0%, rgb(112 78 108 / 30%) 45%, rgb(15, 15, 15) 100%);
    }
}
</style>