<template>
    <section class="section-movie-slide" ref="section">
        <slot name="header"></slot>
        <!-- <Flicking :options="{ align: 'prev', bound: true, bounce: 50, duration: 700, deceleration: 0.0075 }" ref="flicking">
            <template v-for="movie in lists" :key="movie">
                <a class="section-movie-slide__link"
                    :href="'https://m.search.naver.com/search.naver?sm=mtp_hty.top&where=m&query=' + movie.title"
                    target="_blank">
                    <div class="section-movie-slide__poster">
                        <img loading="lazy" :src="require('@/assets/images/poster/' + movie.poster)" alt="">
                    </div>
                    <p class="section-movie-slide__title">{{ movie.title }}</p>
                </a>
            </template>
        </Flicking> -->
        <swiper @swiper="onSwiper" :modules="modules" :freeMode="{momentumBounce: false, momentumBounceRatio: 0, momentumRatio: 0.3, momentumVelocityRatio: 0.5}" :momentum="false" :momentumBounce="false" :slides-per-view="'auto'" :resistanceRatio="0">
            <swiper-slide v-for="movie in lists" :key="movie">
                <a class="section-movie-slide__link"
                    :href="'https://m.search.naver.com/search.naver?sm=mtp_hty.top&where=m&query=' + movie.title"
                    target="_blank">
                    <div class="section-movie-slide__poster">
                        <div class="swiper-lazy-preloader"></div>
                        <img loading="lazy" :src="require('@/assets/images/poster/' + movie.poster)" alt="">
                    </div>
                    <p class="section-movie-slide__title">{{ movie.title }}</p>
                </a>
            </swiper-slide>
        </swiper>
    </section>
</template>
<script>
import { ref } from 'vue'
import { FreeMode } from "swiper";
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/css';
import MovieList from '@/assets/data/movie.json';
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";
gsap.registerPlugin(ScrollTrigger);

export default {
    components: {
        Swiper,
        SwiperSlide,
    },
    setup() {
        const swiperInstance = ref()

        function onSwiper(swiper) {
            swiperInstance.value = swiper
        }

        return {
            modules: [FreeMode],
            onSwiper,
            swiperInstance
        }
    },
    props: {
        movieType: String
    },
    data(){
        return{
            animation: Object
        }
    },
    computed: {
        lists() {
            let movieType = this.movieType;
            let index = getIndex(movieType);
            let list = MovieList.filter(function(items){
                return items.type[index] === movieType ? items : null;
            });

            function getIndex(type){
                return{
                    "korea": 1,
                    "quentin tarantino": 2,
                    "action": 3,
                    "mystery": 3,
                    "horror": 3,
                    "thriller": 3
                }[type] ?? 0;
            }

            function shuffle(array){
                return array.sort(() => Math.random() - 0.5);
            }
            
            return shuffle(list);
        },
    },
    mounted() {
        this.createScrollTrigger();
        ScrollTrigger.refresh();
    },
    unmounted() {
        this.animation.kill();
    },
    methods: {
        slideToFirst(){
            // this.$refs.flicking.stopAnimation();
            // this.$refs.flicking.moveTo(0, 0);
            this.swiperInstance.slideTo(0, 0);
        },
        createScrollTrigger(){
            ScrollTrigger.create({
                id: "animation",
                trigger: this.$refs.section,
                start: "top bottom",
                onLeave: () => {
                    this.slideToFirst();
                },
                onLeaveBack: () => {
                    this.slideToFirst();
                }
            });

            this.animation = ScrollTrigger.getById("animation");
        }
    }
}
</script>
<style lang="scss" scoped>
.flicking-viewport {
    overflow: visible;
}

.section-movie-slide {
    margin: 0 auto 30px;
    padding-left: $left_padding;
    padding-right: $right_padding;
    max-width: 1400px;

    .swiper{
        overflow: visible;
    }

    .swiper-slide{
        margin-right: 8px;
        width: calc(40% - 8px);
        background: rgba(0, 0, 0, 0.6);
        border-radius: 6px;
    }
    .swiper-slide:last-child {
        margin-right: 0;
    }
    .swiper-lazy-preloader{
        border: none;
        animation: none;
        & + img{
            opacity: 0;
        }
    }
    
    &__link {
        cursor: pointer;
        // margin-right: 8px;
        // width: calc(40% - 8px);
        // background: rgba(0, 0, 0, 0.6);
        // border-radius: 6px;
    }
    // &__link:last-child {
    //     margin-right: 0;
    // }
    &__poster {
        @function svgIcon($color){
            $color: '%23'+$color;
            $icon: "data: image/svg+xml, %3Csvg xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' fill='#{$color}' viewBox='0 0 40 32' %3E%3Cpath class='st0' d='M39.1,0.9C38.5,0.3,37.8,0,37,0h-5.6l3.8,7.6h-6.5L25,0h-4.5l3.7,7.6h-6.5L14,0H9.5l3.7,7.6H6.7L3,0C2.2,0,1.5,0.3,0.9,0.9C0.3,1.5,0,2.2,0,3v26c0,0.8,0.3,1.5,0.9,2.1C1.5,31.7,2.2,32,3,32h34c0.8,0,1.5-0.3,2.1-0.9c0.6-0.6,0.9-1.3,0.9-2.1V3C40,2.2,39.7,1.5,39.1,0.9z M37,29H3V10.6h34V29z' /%3E%3C/svg%3E";
            @return url($icon);
        }

        overflow: hidden;
        position: relative;
        padding-top: calc(1440 / 1000 * 100%);
        border-radius: 6px;
        background-color: #121212;
        background-image: svgIcon(424242);
        background-position: center;
        background-size: 34% auto;
        background-repeat: no-repeat;
        
        img{
            display: none;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: opacity ease .5s;
        }
        img[src*="img"] {
            display: block;
        }
    }    
    &__title {
        overflow: hidden;
        padding: 10px 4px;
        text-align: center;
        font-size: 1.4rem;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
}

@media screen and (min-width: 768px) {
    .section-movie-slide {
        .swiper-slide{
            width: calc(28% - 8px);
        }
        // &__link {
        //     width: calc(28% - 8px);
        // }
    }
}
@media screen and (min-width: 1024px) {
    .section-movie-slide {
        .swiper-slide{
            width: calc(22% - 8px);
        }
        // &__link {
        //     width: calc(22% - 8px);
        // }
    }
}
</style>