<template>
    <button @click="scrollTop" class="scrolltop" data-scrolltop="false">
        <svg xmlns="http://www.w3.org/2000/svg" x="0px" y="0px" viewBox="0 0 32 32">
            <path d="M14.5,32V5.7L2.1,18.1L0,16L16,0l16,16l-2.1,2.1L17.5,5.7V32H14.5z" />
        </svg>
    </button>
</template>
<script>
export default {
    methods: {
        scrollTop: ()=> {
            window.scrollTo({ top: 0, left: 0, behavior: 'smooth' });
        }
    },
    mounted() {
        const scrolltop = document.querySelector('.scrolltop');
        let lastKnownScrollPosition = 0;
        let ticking = false;

        // Showing or Hiding
        function toggle(scrollPos) {
            scrollPos > window.innerHeight ? scrolltop.setAttribute('data-scrolltop', true) : scrolltop.setAttribute('data-scrolltop', false);
        }

        function runOnScroll(scrollPos) {
            toggle(scrollPos);
        }

        document.addEventListener('scroll', (e) => {
            lastKnownScrollPosition = window.scrollY;

            if (!ticking) {
                window.requestAnimationFrame(() => {
                    runOnScroll(lastKnownScrollPosition);
                    ticking = false;
                });

                ticking = true;
            }
        });
    },
}
</script>
<style lang="scss" scoped>
.scrolltop {
    z-index: 10;
    position: fixed;
    right: 20px;
    bottom: 20px;
    padding: 16px;
    width: 50px;
    height: 50px;
    color: #fff;
    background: $color_primary;
    border-radius: 50%;
    opacity: 0;
    transform: translateY(50px);

    &[data-scrolltop="false"] {
        pointer-events: none;
    }

    &[data-scrolltop="true"] {
        animation: scrollFadeIn .5s ease forwards;
    }

    svg {
        display: block;
        width: 100%;
        height: 100%;
        fill: currentColor;
    }
}
</style>